@import "src/common/styles/sass/variables.scss";

.footerBlock {
  padding: 20px 0 27px;
  background: $bgColor;
  text-align: center;

  .footerContainer {
    padding-right: 15px;
    padding-left: 15px;
    margin-right: auto;
    margin-left: auto;
    position: relative;

    .footerIcon {
      position: relative;
      min-height: 1px;
      padding-right: 15px;
      padding-left: 15px;
      float: left;
      text-align: center;
      font-family: $textFontFamily;
      line-height: 26px;
      font-size: 14px;
      color: #333;

      .p {
        font-size: 16px;
        color: $titleColor;
        margin: 0;
        padding: 0;
        text-align: center;
        line-height: 26px;
      }

      .socialIcon {
        li {
          display: inline-block;
          list-style: none;

          a {
            color: $titleColor;
            background: rgba(42, 41, 41, 0);
            font-size: 1.3em;
            display: inline-block;
            padding: 10px 10px 8px 10px;
            width: 50px;
            height: 40px;
            box-sizing: border-box;
            transition: all ease-in-out 0.3s;
            margin-bottom: 0;
            text-decoration: none;
            position: relative;

          }
        }
      }
    }
  }
}







